import { registerBlockType } from "@wordpress/blocks";
import {
  useBlockProps,
  RichText,
  InspectorControls,
  MediaUpload,
} from "@wordpress/block-editor";
import {
  PanelBody,
  TextControl,
  Button,
  TextareaControl,
} from "@wordpress/components";
import { useState } from "@wordpress/element";

registerBlockType("custom/tabs", {
  title: "Custom Tabs",
  icon: "menu",
  category: "widgets",
  attributes: {
    tabs: {
      type: "array",
      default: [],
    },
    activeTab: {
      type: "number",
      default: 0,
    },
  },

  edit: ({ attributes, setAttributes }) => {
    const { tabs = [], activeTab } = attributes;

    const addTab = () => {
      const newTabs = [
        ...tabs,
        { title: "New Tab", content: "Tab Content", icon: "" },
      ];
      setAttributes({ tabs: newTabs });
    };

    const updateTab = (index, field, value) => {
      const newTabs = tabs.map((tab, i) =>
        i === index ? { ...tab, [field]: value } : tab
      );
      setAttributes({ tabs: newTabs });
    };

    return (
      <div {...useBlockProps()}>
        <InspectorControls>
          <PanelBody title="Tab Settings">
            {tabs.length > 0 ? (
              tabs.map((tab, index) => (
                <div
                  key={index}
                  style={{
                    marginBottom: "15px",
                    borderBottom: "1px solid #ccc",
                    paddingBottom: "10px",
                  }}
                >
                  <TextControl
                    label="Tab Title"
                    value={tab.title}
                    onChange={(value) => updateTab(index, "title", value)}
                  />
                  <TextareaControl
                    label="Tab Content"
                    value={tab.content}
                    onChange={(value) => updateTab(index, "content", value)}
                  />
                  <MediaUpload
                    onSelect={(media) => updateTab(index, "icon", media.url)}
                    allowedTypes={["image/svg+xml"]}
                    render={({ open }) => (
                      <Button isPrimary onClick={open}>
                        {tab.icon ? "Change Icon" : "Upload Icon"}
                      </Button>
                    )}
                  />
                  {tab.icon && (
                    <img
                      src={tab.icon}
                      alt="Tab Icon"
                      style={{ width: "50px" }}
                    />
                  )}
                </div>
              ))
            ) : (
              <p>No tabs added yet. Click "Add Tab" to create one.</p>
            )}
            <Button isSecondary onClick={addTab}>
              Add Tab
            </Button>
          </PanelBody>
        </InspectorControls>

        <div className="custom-tabs-editor">
          <ul className="tabs">
            {tabs.map((tab, index) => (
              <li
                key={index}
                className={`tab ${index === activeTab ? "active" : ""}`}
                onClick={() => setAttributes({ activeTab: index })}
              >
                {tab.icon && (
                  <img
                    src={tab.icon}
                    alt={tab.title}
                    style={{ width: "24px", marginRight: "5px" }}
                  />
                )}
                {tab.title}
              </li>
            ))}
          </ul>
          <div className="tab-content">
            {tabs.length > 0
              ? tabs[activeTab]?.content || "No content"
              : "No tabs added yet"}
          </div>
        </div>
      </div>
    );
  },

  save: ({ attributes }) => {
    const { tabs = [], activeTab } = attributes;

    return (
      <div className="custom-tabs">
        <ul className="tabs">
          {tabs.map((tab, index) => (
            <li
              key={index}
              className={`tab ${index === activeTab ? "active" : ""}`}
            >
              {tab.icon && (
                <img
                  src={tab.icon}
                  alt={tab.title}
                  style={{ width: "24px", marginRight: "5px" }}
                />
              )}
              {tab.title}
            </li>
          ))}
        </ul>
        <div className="tab-content">
          {tabs.map((tab, index) => (
            <div
              key={index}
              className={`tab-pane ${index === activeTab ? "active" : ""}`}
            >
              {tab.content}
            </div>
          ))}
        </div>
      </div>
    );
  },
});
